body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; 
    background-image: url(/ClickyGame/static/media/Emoji-background.2b245ee2.png);
    display: -webkit-flex;
    display: flex;
    height: 100%;
    overflow: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.column {
  height: 210px;
  width: 200px !important;
  padding-left: 10px;
  padding-right: 10px;
}
.navbar {
    position: -webkit-sticky;
    position: sticky;
    background-color: firebrick ;
    vertical-align: auto;
    height: 0.5%;
    opacity: 1;
    border-color: steelblue;
    border-style: outset;
    border-width: 3px;
}

.navbar li {
    display: inline-block;
    padding-top: 10px;
    text-align: center;
    color: white;
    font-weight: bold;

}

.itemLeft {
    width: 40%;
    font-size: 32px;
    /* padding-left: 5px; */
}

.itemCenter {
    font-size: 20px;
    width: 30%;
}

.itemRight {
    width: 30%;
    font-size: 20px;
}


.container {
    -webkit-align-content: center;
            align-content: center;
    background-color: white;
    opacity: 0.9;
    padding: 1%;
    margin-top: 1%;
    margin-bottom: 10px;
    font-style: italic;
    border-color: steelblue;
    border-style: outset;
    border-width: 10px;
}

.header {
    text-align: center;
}

.h2 {
    -webkit-justify-content: center;
            justify-content: center;
}
.card {
    height: 200px;
    width: 200px !important;
}

.image {
    height: 200px;
    width: 200px;
    padding: 50px;
    opacity: 0.80;
    border-color: steelblue;
    border-style: outset;
    border-width: 10px;
}
